

.list{
    color:#fff;
    width:300px;
    background-color:#bf07ac;
    height:50px;
    margin:20px auto;
    border-radius:5px;
}

.list p{
    padding:10px;
    position:relative;
   
}

.list p span{
    position:absolute;
    right:10px;
    cursor:pointer;

}

.faicons{
    margin: 0 5px;
    font-size:15px;
}

.list p input{
    background-color:transparent;
    border:0;
    color:#fff;
    font-size:17px;
    transition:all ease-in 0.5s;
    font-family:Georgia, 'Times New Roman', Times, serif;
}

.list p input:focus{
    outline:none;
    font-weight:bold;
    font-size:19px;
    /* transition:all ease-in 0.8s; */

}

